<template>
  <div class="formcollection">
    <!-- gray bar  -->
    <GrayBar />
    <div class="form_box">
      <h1>贵州省成考/自考/国开在线预报名</h1>
      <ul>
        <li>
          <nut-picker
            :is-visible="isVisible_SchoolType"
            :list-data="typeData"
            @close="switchPicker_SchoolType('isVisible_SchoolType')"
            @confirm="setSchoolType"
            title="请选择报考类型"
          ></nut-picker>
          <div
            class="copyselect"
            @click="up_schooltype"
          >{{SchoolType == '' ? '请选择报考类型' : SchoolType}}</div>
          <i class="iconfont">&#xe65b;</i>
        </li>
        <li>
          <nut-picker
            :is-visible="isVisible_SchoolLevel"
            :list-data="levelData"
            @close="switchPicker_SchoolLevel('isVisible_SchoolLevel')"
            @confirm="setSchoolLevel"
            title="请选择层次"
          ></nut-picker>
          <div
            class="copyselect"
            @click="up_leveltype"
          >{{SchoolLevel == '' ? '请选择层次' : SchoolLevel}}</div>
          <i class="iconfont">&#xe65b;</i>
        </li>
        <li>
          <nut-picker
            :is-visible="isVisible_study"
            :list-data="studyData"
            @close="switchPicker_study('isVisible_study')"
            @confirm="setstudy"
            title="请选您的学历"
          ></nut-picker>

          <div class="copyselect" @click="up_study">{{study == '' ? '请选您的学历' : study}}</div>

          <i class="iconfont">&#xe65b;</i>
        </li>
        <li>
          <nut-picker
            :is-visible="isVisible_major"
            :list-data="majorData"
            @close="switchPicker_major('isVisible_major')"
            @confirm="setmajor"
            title="请选您的学历"
          ></nut-picker>

          <div class="copyselect" @click="up_major">{{major == '' ? '报考专业' : major}}</div>

          <i class="iconfont">&#xe65b;</i>
        </li>
        <li>
          <nut-textinput
            placeholder="您的姓名"
            @focus="onFocus_name"
            @blur="onBlur_name"
            v-model="username"
          />
        </li>
        <li>
          <nut-textinput
            placeholder="您的联系电话"
            @focus="onFocus_phone"
            @blur="onBlur_phone"
            v-model="userphone"
          />
        </li>
        <li>
          <nut-button block>提交报名</nut-button>
        </li>
      </ul>

      <div class="need_know">
        <p>
          <span>报考须知：</span> 贵州省最后一次免入学考试报名，请考生填写真实有效的信息，以免错报漏报，招生老师会第一时间审核考生信息
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import GrayBar from "../components/GrayBar";
export default {
  name: "formcollection",
  data() {
    return {
      isVisible_SchoolType: false,
      SchoolType: "",
      typeData: [["成考", "自考"]],

      isVisible_SchoolLevel: false,
      SchoolLevel: "",
      levelData: [["研究生", "本科"]],

      isVisible_study: false,
      study: "",
      studyData: [["初中", "高中", "大专"]],

      isVisible_major: false,
      major: "",
      majorData: [["教育", "技师", "主播"]],

      username: "",
      userphone: "",
    };
  },
  methods: {
    up_schooltype() {
      this.isVisible_SchoolType = !this.isVisible_SchoolType;
    },
    switchPicker_SchoolType(param) {
      this[`${param}`] = !this[`${param}`];
    },
    setSchoolType(chooseData) {
      this.SchoolType = `${chooseData[0]}`;
      this[`${chooseData}`] = !this[`${chooseData}`];
    },

    up_leveltype() {
      this.isVisible_SchoolLevel = !this.isVisible_SchoolLevel;
    },
    switchPicker_SchoolLevel(param) {
      this[`${param}`] = !this[`${param}`];
    },
    setSchoolLevel(chooseData) {
      this.SchoolLevel = `${chooseData[0]}`;
      this[`${chooseData}`] = !this[`${chooseData}`];
    },

    up_study() {
      this.isVisible_study = !this.isVisible_study;
    },
    switchPicker_study(param) {
      this[`${param}`] = !this[`${param}`];
    },
    setstudy(chooseData) {
      this.study = `${chooseData[0]}`;
      this[`${chooseData}`] = !this[`${chooseData}`];
    },

    up_major() {
      this.isVisible_major = !this.isVisible_major;
    },
    switchPicker_major(param) {
      this[`${param}`] = !this[`${param}`];
    },
    setmajor(chooseData) {
      this.major = `${chooseData[0]}`;
      this[`${chooseData}`] = !this[`${chooseData}`];
    },

    onFocus_name() {},
    onBlur_name() {},
    onFocus_phone() {},
    onBlur_phone() {},
  },
  components: {
    GrayBar,
  },
};
</script>

<style lang='scss' scoped>
div.formcollection {
  div.form_box {
    padding: 24px 15px;
    box-sizing: border-box;
    h1 {
      font-size: 18px;
      font-weight: bold;
      color: rgba(0, 0, 0, 1);
      text-align: center;
      margin: 0;
    }
    ul {
      li {
        padding-top: 20px;
        position: relative;
        & > div.copyselect {
          width: 100%;
          height: 100%;
          height: 40px;
          line-height: 40px;
          font-size: 14px;
          box-sizing: border-box;
          border-radius: 4px;
          border: 1px solid #dddfe6;
          background-color: "#FFFFFF";
          padding: 0 30px 0 10px;
          color: #333;
        }
        i {
          position: absolute;
          top: 50%;
          transform: translateX(-50%);
          right: 3px;
          color: #999;
        }
      }
    }

    div.need_know {
      padding-top: 24px;
      p {
        font-size: 14px;
        line-height: 1.8;
        color: #000000;
        span {
          font-weight: bold;
          color: rgba(221, 61, 61, 1);
          font-size: 16px;
        }
      }
    }
  }
}
</style>
<style lang="scss">
.formcollection {
  .nut-button {
    background: linear-gradient(315deg, #60c9fa, #409eff);
  }
  input::placeholder {
    color: #333;
  }
}
</style>